import Json from '@/Json';
import empty from '@/components/empty.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	components:{
		empty,
		uniLoadMore
	},
	data() {
		return {
			currentIndex: 0,    // 头部选项卡初始选中位置
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '售后',
					loadingType: 'more',
					orderList: []
				}
			]
		};
	},
	onLoad() {
		this.loadData()
	},
	methods:{
		// 头部点击换颜色方法
		tabClick(index){
			this.currentIndex = index;
		},
		// 获取订单列表
		loadData(source){
			let index = this.currentIndex;
			let navItem = this.navList[index];
			let state = navItem.state
			console.log(index,navItem,state)
			// 与下面一样的请求到数据一样的
			// this.$api.json('orderList').then(res =>{
			// 	console.log(res)
			// })
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据,数据处理一次，下面的就不执行了，return掉了
				return;
			}
			
			// 很重要，不然一直出现转圈
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			
			setTimeout(() =>{
				let orderList = Json.orderList.filter(item =>{
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.state));
					console.log(item)
					// 一定要return，不然最后的console.log(orderList)打印不到内容
					// 如果state为0得话，就返回所有订单
					if(state === 0){
						return item
					}
					// 否则就返回后台中数据中订单得state与用户选则得tab栏中的state相同得数据
					// 其实下面的item.state === state 就是一个条件筛选，所以要用filter方法
					return item.state === state
				})
				// console.log(orderList)
				orderList.forEach((item) =>{
					navItem.orderList.push(item)
				})
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				this.$set(navItem,'loaded',true)
				console.log(this.navList)
				
				//判断是否还有数据， 有改为 more， 没有改为noMore
				navItem.loadingType = 'nomore';
			},600)
		},
		// tab栏切换
		changeTab(e){
			console.log(e)
			this.currentIndex = e.detail.current
			this.loadData('tabChange')
		},
		// 删除订单
		delOrder(index){
			// 先找到是全部还是待付款，待评价，然后找到里面要删除哪一个
			uni.showLoading({
				title:'请稍后'
			})
			setTimeout(() =>{
				this.navList[this.currentIndex].orderList.splice(index,1);
				uni.hideLoading()
			},600)
			
		},
		// 取消订单
		cancelOrder(item){
			console.log(item);
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() =>{
				let {stateTip, stateTipColor} = this.orderStateExp(9);
				console.log(stateTip,stateTipColor)
				item = Object.assign(item,{
					state: 9,
					stateTip,
					stateTipColor
				})
				
				// 状态更改完之后，删除待付款中的相同订单
				let order = this.navList[1].orderList;
				let index = order.findIndex(item =>item.state == 9)
				order.splice(index,1)
				
				uni.hideLoading()
			},1000)
			
		},
		// 给每个商品添加颜色属性，订单状态属性
		orderStateExp(state){
			let stateTip='',
			stateTipColor = '#fa436a';
			switch(state){
				case 1:
				stateTip = '待付款'; break;
				case 2:
				stateTip = '待发货'; break;
				case 9:
				stateTip = '订单已关闭';
				stateTipColor = '#909399';
				break;
			}
			return {stateTip,stateTipColor};
		}
	}
}
